import app from './_firebase';


export const listAllCategory = async () => {
  try {
    let querySnapshot = await app.firestore().collection('categories').get();
    const category = querySnapshot.docs.map((doc) => {
      return  doc.id;
    });
    return category
  } catch (err) {
    console.log(err);
  }
};
export const listSchools = async () => {
  try {
    let querySnapshot = await app.firestore().collection('schools').get();
    const schools = querySnapshot.docs.map((doc) => {
      let result = {};
      result.id = doc.id;
      result.name = doc.data().name;
      return result;
    });
    return schools
  } catch (err) {
    console.log(err);
  }
};

