export const addCategory = async (data) => {
  try {
    let category = await app.firestore().collection('categories').add(data);
    console.log(category.data);
  } catch (err) {
    console.log(err);
  }
};

export const deleteCategorys = (ids) => {
  try {
    ids.map((id) => {
      app.firestore().collection('categories').doc(id).delete();
    })
  } catch (err) {
    console.log(err);
  }
};

export const getCategoryApi = async (id) => {
  try {
    let querySnapshot = await app.firestore().collection('categories').doc(id).get();
    const category = querySnapshot.data();
    category.id = id;
    return category
  } catch (err) {
    console.log(err);
  }
}
export const updateCategory = async (data) => {
  try {
    console.log("update categories");
    let id = data.id;
    let result = await app.firestore().collection('categories').doc(id).set(data);
    console.log(result);
    return result
  } catch (err) {
    console.log(err);
  }
};
export const listAllTags = async () => {
  try {
    let querySnapshot = await app.firestore().collection('tags').where("root","==",true).get();
    const tags = querySnapshot.docs.map((doc) => {
      return  doc.id;
    });
    return tags
  } catch (err) {
    console.log(err);
  }
}
