import React, {
  useState,
  useEffect,
  useCallback
} from 'react';
import PropTypes from 'prop-types';
import clsx from 'clsx';
import * as Yup from 'yup';
import { Formik } from 'formik';
import { useSnackbar } from 'notistack';
import {
  Box,
  Button,
  Card,
  CardContent,
  Grid,
  TextField,
  makeStyles,
  CardHeader,
  Divider,
  Paper,
  FormHelperText,
  FormControlLabel,
  Checkbox,
  RadioGroup,
  Radio,
  Typography,
  Tooltip
} from '@material-ui/core';

import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import wait from 'src/utils/wait';



import useIsMountedRef from 'src/hooks/useIsMountedRef';
import { useHistory } from 'react-router-dom';
import { gStatus } from '../../../api/categories';
import { addLivestreamVideo } from '../../../api/livestreams';

const useStyles = makeStyles(() => ({
  brief: {
    '& .ql-editor': {
      height: 150
    }
  },
  tags: {
    position: 'relative',
    overflow: 'auto',
    maxHeight: 300
  },
  categories: {
    position: 'relative',
    overflow: 'auto',
    maxHeight: 300
  }
}));

const VideoCreateForm = ({ className, ...rest }) => {
  const history = useHistory();
  const isMountedRef = useIsMountedRef();
  const classes = useStyles();
  const [live, setStatus] = useState([]);
  const getStatus = useCallback(async () => {
    try {
      const status = gStatus();
      console.log(status);
      if (isMountedRef.current) {
        setStatus(status);
      }
    } catch (err) {
      console.error(err);
    }
  }, [isMountedRef]);

  useEffect(() => {
    getStatus();
  }, [getStatus]);

  return (
    <Formik
      initialValues={{
        title: '',
        url: '',
        live: '',
        submit: null
      }}
      validationSchema={Yup.object().shape({
        title: Yup.string().max(255)
      })}
      onSubmit={async (values, {
        resetForm,
        setErrors,
        setStatus,
        setSubmitting
      }) => {
        try {
          console.log(values);
          // NOTE: Make API request
          addLivestreamVideo(values);
          await wait(500);
          history.push('/teacher/management/livestream');
        } catch (err) {
          console.error(err);
          setStatus({ success: false });
          setErrors({ submit: err.message });
          setSubmitting(false);
        }
      }}
    >
      {({
          errors,
          handleBlur,
          handleChange,
          handleSubmit,
          setFieldValue,
          isSubmitting,
          touched,
          values
        }) => (
        <form className={clsx(classes.root, className)} onSubmit={handleSubmit}{...rest}>
          <Card>
            <CardContent>
              <Grid container spacing={3}>
                <Grid item md={6} xs={12}>
                  <TextField
                    error={Boolean(touched.title && errors.title)}
                    fullWidth
                    helperText={touched.title && errors.title}
                    label="Tiêu đề"
                    name="title"
                    onBlur={handleBlur}
                    onChange={handleChange}
                    required
                    value={values.title}
                    variant="outlined"
                  />
                </Grid>

                <Grid item md={6} xs={12}>
                  <TextField
                    error={Boolean(touched.content_type && errors.content_type)}
                    fullWidth
                    helperText={touched.content_type && errors.content_type}
                    label="Loại Content"
                    name="content_type"
                    onBlur={handleBlur}
                    onChange={handleChange}
                    required
                    disabled
                    value="livestream"
                    variant="outlined"
                  />
                </Grid>
              </Grid>
              <br/>
              <Grid item md={12} md={12} xs={12}>
                <TextField
                  error={Boolean(touched.description && errors.description)}
                  fullWidth
                  helperText={touched.description && errors.description}
                  label="Link video"
                  name="url"
                  onBlur={handleBlur}
                  onChange={handleChange}
                  required
                  multiline
                  value={values.description}
                  variant="outlined"
                />
              </Grid>
              <br/>
              <Grid item md={4} md={4} xs={12}>

                <Typography gutterBottom variant="h6" color="textPrimary">
                  Status
                </Typography>

                <Typography gutterBottom variant="body2" color="textSecondary">
                  Chọn trạng thái
                </Typography>

                <List className={classes.categories}>
                  <RadioGroup aria-label="live" name="live" value={values.status} onChange={handleChange}>
                    {live.map((value) => {
                      return (
                        <ListItem key={value} role={undefined} dense button>
                          <FormControlLabel value={value} control={<Radio/>} label={value}/>
                        </ListItem>
                      );
                    })}
                  </RadioGroup>
                </List>

              </Grid>
              {(touched.detail && errors.detail) && (
                <Box mt={2}>
                  <FormHelperText error>
                    {errors.detail}
                  </FormHelperText>
                </Box>
              )}
              <Box mt={2}>
                <Button variant="contained" color="secondary" type="submit" disabled={isSubmitting}>
                  Lưu
                </Button>
              </Box>
            </CardContent>
          </Card>
        </form>
      )}
    </Formik>
  );
};

VideoCreateForm.propTypes = {
  className: PropTypes.string
};

export default VideoCreateForm;
